.input {
  background: #ffffff;
  border-radius: 2px;
  padding: 18px 20px;
  border: 2px solid transparent;
  outline: none;
  color: #313037;
  width: 100%;
  font-size: 16px;
  font-weight: 400;
  resize: vertical;
  &:focus {
    border-color: #dadada;
  }
  &:hover {
    border-color: #dadada;
  }
  &:disabled {
    background: #e8e8e8;
    color: #8d8e97;
  }
}

.error {
  border-color: #fd3419;
}
